import React, { useState } from 'react';
import { Link, useRouteMatch } from "react-router-dom";

function ResetPass() {

  const [password, setPassword] = useState("");
  const [fetching, setFetching] = useState(false);
  const [done, setDone] = useState(false);

  const url = useRouteMatch();
  const { params } = url;
  const searchParams = new URLSearchParams(params.algo);
  const resetKey = searchParams.get("resetKey");
  const id = searchParams.get("id");

  return (
    <main className="reset-form">
      <h3 className="title">Reset Password</h3>
      {done ?
      <>
      
      <p className="done">The password has been updated</p>
      <Link to="/login">Login</Link>
      </>
        :
        <form>
          <input onChange={(e) => setPassword(e.target.value)} value={password} type="password" placeholder="Password" />
          <button onClick={sendUpdatedPassword} className="reset-password">Reset Password</button>
        </form>
      }
      {fetching && <p className="loading">Is loading...</p>}
    </main>
  )

  function sendUpdatedPassword(e) {
    e.preventDefault();
    if (fetching) return;
    setFetching(true);
    fetch("/api/users/reset", {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      body: JSON.stringify({ password, resetKey, id })
    })
      .then(res => res.json())
      .then(result => {
        console.log(result);
        if(!result.error) setDone(true);
      })
      .catch(err => console.log(err))
      .finally(() => {
        setFetching(false);
      })
  }
}

export default ResetPass
