import React, { useState } from 'react'

function EditTopic({ value, id, setEditMode, setIntro }) {

  const [editorValue, setEditorValue] = useState(value);

  return (
    <>
      <textarea onChange={(e) => setEditorValue(e.target.value)} value={editorValue} />
      <div className="edit-actions">
        <button onClick={editTopic} className="confirm">Yes</button>
        <button onClick={() => setEditMode(false)} className="cancel">No</button>
      </div>
    </>
  )

  function editTopic() {
    fetch("/api/topics/", {
      method: "PUT",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      body: JSON.stringify({
        content: editorValue,
        id
      })
    })
      .then(res => res.json())
      .then(() => {
        setIntro(editorValue);
        setEditorValue("")
        setEditMode(false);
      })
      .catch(err => { if (err) console.log(err) })
  }
}

export default EditTopic
