import React, { useState } from 'react'

function SendReset() {

  const [email, setEmail] = useState("");
  const [fetching, setFetching] = useState(false);
  const [done, setDone] = useState(false);

  return (
    <main className="reset-form">
      <h3 className="title">Reset Password</h3>
      {done ?
        <p className="done">The email has been sent. Go to check it out</p>
        :
        <form>
          <input onChange={(e) => setEmail(e.target.value)} value={email} type="email" placeholder="Email" />
          <button onClick={sendResetPassRequest} className="reset">Send Reset Request</button>
        </form>
      }
      {fetching && <p className="loading">Is loading...</p>}
    </main>
  )

  function sendResetPassRequest(e) {
    e.preventDefault();
    if (fetching) return;
    setFetching(true);
    fetch("/api/users/reset", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        "Accept": "application/json"
      },
      body: JSON.stringify({ email })
    })
      .then(res => res.json())
      .then(result => {
        console.log(result);
        if(!result.error) setDone(true);
      })
      .catch(err => console.log(err))
      .finally(() => {
        setFetching(false);
      })
  }
}

export default SendReset
