import React from 'react'

function AddComment({ ref, addChild, setCommentMode, setCommenting, answerContent, onChange }) {
  return (
    <>
      <input ref={ref} value={answerContent} type="text" className="answer-comment" onChange={onChange} />
      <button onClick={addChild} className="send-answer-comment">Send</button>
      <button onClick={() => {
        setCommentMode(false);
        setCommenting(false);
      }} className="abort-answer-comment">&times;</button>
    </>
  )
}

export default AddComment
