import React, { useRef, useState } from 'react'
import { client } from '../../App';
import { openImagePicker, updateProfileImage } from '../logic/uploadImage';

function AvatarPicker({ user, setUser }) {

  const [open, setOpen] = useState(false);
  const avatarImageEl = useRef();
  const [fetching, setFetching] = useState(false);
  const form = useRef();

  return (
    <>
      <form ref={form}>
        <div className="avatar-section">
          <img ref={avatarImageEl} onClick={openImagePicker} className="image-picker" src={user.profile_image} alt="Profile" />
          <i className="fas fa-camera change-image"></i>
          <input name="avatar" onChange={toggleImageOnClient} className="image-picker-input" accept="image/*" type="file" />
          {fetching && <p className="loading">The image is uploading</p>}
        </div>
        {open &&
          <div className="image-actions">
            <button className="confirmUpdateImage" onClick={sendUpdatedProfileImage}><i className="fas fa-check yes"></i></button>
            <button className="cancelUpdateImage" onClick={cancelImagePicker}><i className="fas fa-times"></i></button>
          </div>
        }
      </form>
    </>
  )

  function toggleImageOnClient(e) {
    if (e.target.files[0]) updateProfileImage(e, avatarImageEl.current);
    setOpen(true);
    if (e.target.files[0] === undefined) {
      avatarImageEl.current.src = user.profile_image;
      setOpen(false);
    }
  }

  function cancelImagePicker() {
    setOpen(false);
    avatarImageEl.current.src = user.profile_image;
    if (document.querySelector(".image-picker-input")) document.querySelector(".image-picker-input").value = "";
  }

  function sendUpdatedProfileImage(e) {
    e.preventDefault();
    const imageData = new FormData(form.current);
    console.log(document.forms[1]);
    setFetching(true);
    fetch("/api/profile/avatar", {
      method: "PUT",
      body: imageData
    })
      .then(res => res.json())
      .then(result => {
        setUser({ ...user, profile_image: result.path });
        client.refetchQueries("userdata"); //Destroy the results of global User for refreshing
      })
      .catch(err => alert(err))
      .finally(() => {
        setOpen(false);
        setFetching(false);
      })
  }
}

export default AvatarPicker
