import React from 'react'

function EditComment({ ref, editComment, setEditMode, setCommenting, answerContent, onChange }) {
  return (
    <>
      <input ref={ref} value={answerContent} type="text" className="answer-comment" onChange={onChange} />
      <button onClick={editComment} className="send-answer-comment"><i className="fas fa-check yes"></i></button>
      <button onClick={() => {
        setEditMode(false);
        setCommenting(false);
      }} className="abort-answer-comment">&times;</button>
    </>
  )
}

export default EditComment
