import { useState } from "react";

export default async function randomTopic(){
  const result = await fetch("/api/topics").then(res => res.json()).catch(err => { console.log(err); return });
  const topics = result.data.map(topic => topic.id);
  if(topics.length === 0) return false;
  const randomIndex = Math.floor(Math.random() * topics.length);
  return topics[randomIndex];
}